<template>
    <div class="project3">
        <BackHome/>
        <Loader/>
        <Banner/>
        <BasicDescription/>
        <Showcase/>
        <Information/>
        <Phones/>
        <Contact/>
        <Next/>
    </div>
</template>

<script>
import BackHome from '../components/BackHome.vue'
import Loader from '../components/Loader.vue'
import Banner from '../components/p3Comp/Banner.vue'
import BasicDescription from '../components/p3Comp/BasicDescription.vue'
import Showcase from '../components/p3Comp/Showcase.vue'
import Information from '../components/p3Comp/Information.vue'
import Phones from '../components/p3Comp/Phones.vue'
import Contact from '../components/Contact.vue'
import Next from '../components/p3Comp/Next.vue'

export default {
    name:'Project3',
    components:{
        BackHome,
        Loader,
        Banner,
        BasicDescription,
        Showcase,
        Information,
        Phones,
        Contact,
        Next
    },
    created () {
        window.addEventListener('scroll', this.handleScroll);
    },
    destroyed () {
        window.removeEventListener('scroll', this.handleScroll);
    },
    methods: {
        handleScroll (event) {
            let phone1 = document.querySelector('.phone1')
            let phone2 = document.querySelector('.phone2')
            let value = window.scrollY
            phone1.style.marginTop = value / -700 + 'rem'
            phone2.style.marginTop = value / 700 + 'rem'
            //numbers.forEach(number => number.style.marginTop = value / -400 + 'rem')
           
        }
    }
}
</script>

